.greetingForm {
    margin-bottom: 50px;
}

.inputAndButtonContainer {
    display: flex;
    flex-direction: row;
    gap: 10px;
}

.error {
    color: red;
    margin-top: 10px;
    margin-bottom: 10px;
}

.input {
    width: 375px;
    height: 35px;
    border-radius: 3px;
    margin-right: 10px;
}

.input:focus {}

.errorInput {
    border: 1px solid red;
    width: 375px;
    height: 35px;
    border-radius: 3px;
    margin-right: 10px;
}

.button {
    width: 78px;
    height: 36px;
    background-color: #0066CC;
    color: white;
}

.button:focus {}

.button:active {}

.button:disabled {}

.text {
    font-weight: 400;
    line-height: 17px;
    text-align: left;
    color: #00000080;
    margin-top: 10px;
    margin-bottom: 10px;
}

.greeting {
    margin-top: 10px;
}